$(document).on('click', '.qa-card__show', function(e) {
    $(this).parents(".qa-card").toggleClass("_active")
    $(this).siblings(".qa-card__hidden").slideToggle()
})

$("._toggle-product-detail").click(function () {
    $(this).parents(".product-detail__item").toggleClass("_active")
    $(this).siblings(".product-detail__hidden").slideToggle()
})

$("._show-more-detail").click(function () {
    $(".catalog-detail").find("[data-tab='_tab1']").click()
})
