function formatNums(el) {
    let num = +$(el).attr("data-num")
    $(el).html(num.toLocaleString())
}

$(".cart-row").each(function (i,el) {
    let input = $(el).find(".cart-row__count input")
    let price = $(el).find("._price")
    let priceSum = $(el).find("._price-sum")

    formatNums(price)
    formatNums(priceSum)

    $(input).change(function () {
        let val = $(this).val()
        let allSum = +$(price).attr("data-num") * val
        $(priceSum).attr("data-num",allSum)
        formatNums(priceSum)
    })
})

$(".order-row").each(function (i,el) {
    let input = $(el).find(".order-row__count input")
    let price = $(el).find("._price")
    let priceSum = $(el).find("._price-sum")

    formatNums(price)
    formatNums(priceSum)

    $(input).change(function () {
        let val = $(this).val()
        let allSum = +$(price).attr("data-num") * val
        $(priceSum).attr("data-num",allSum)
        formatNums(priceSum)
        sumOfOrder()
    })


})

sumOfOrder()
function sumOfOrder() {
    let sum = 0
    $(".order-row").each(function (i,el) {
        let priceSum = +$(el).find("._price-sum").attr("data-num")

        sum = sum + priceSum
    })


    $("._order-sum").attr("data-num",sum)
    formatNums($("._order-sum"))
}

$("._toggle-auth").click(function () {
    $("body").toggleClass("_open-auth")
    $("body").toggleClass("fixed-body")
})

$(".favourite-nav__show").click(function () {
    $(this).parents(".favourite-nav").toggleClass("_open")
})
