$(window).on('load resize', function(){
    if ($(window).width() <= 992) {
        $('.catalog__title').insertBefore('.catalog__grid');
    } else{
        $('.catalog__title').insertBefore('.catalog-search');
    }
});

let filterSliders = document.querySelectorAll(".filter-slider")

filterSliders.forEach(slider => {
    const rangeSlider = slider.querySelector(`.range-slider`)
    const rangeOutput1 = slider.querySelector(`._output1`)
    const rangeOutput2 = slider.querySelector(`._output2`)
    const rangeOutputs = [rangeOutput1, rangeOutput2]

    let startValue1 = +slider.getAttribute("data-start")
    let startValue2 = +slider.getAttribute("data-start2")
    let min = +slider.getAttribute("data-min")
    let max = +slider.getAttribute("data-max")

    rangeOutput1.innerHTML = startValue1.toLocaleString()
    rangeOutput2.innerHTML = startValue2.toLocaleString()

    if (rangeSlider) {
        noUiSlider.create(rangeSlider, {
            start: [startValue1, startValue2],
            connect: true,
            step: 1,
            range: {
                'min': min,
                'max': max
            }
        });
    }
    if (rangeSlider) {
        rangeSlider.noUiSlider.on('update', function(values, handle) {
            rangeOutputs[handle].innerHTML = Math.round(values[handle]).toLocaleString()
        })
    }
    const setRangeSlider = (i, value) => {
        let array = [null, null];
        array[i] = value;

        rangeSlider.noUiSlider.set(array);
    };
})

$(".filter-group__top").click(function () {
    $(this).parents(".filter-group").toggleClass("_open")
    $(this).siblings(".filter-group__bottom").slideToggle()
})

$("._toggle-filter").click(function () {
    $("body").toggleClass("_open-filter")
    $("body").toggleClass("fixed-body")
})
