$("._toggle-menu").click(function () {
    $(".header").toggleClass("_open-menu")
    $("body").toggleClass("fixed-body")
})

$(".lang__show").click(function () {
    $(this).parents(".lang").toggleClass("_open")
})

$(document).mouseup( function(e){
    var div = $( ".lang,.header-contacts,.favourite-nav" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) { // и не по его дочерним элементам
        if($(div).hasClass("_open")){
            $(div).removeClass("_open")
        }
    }
});


$(".header-contacts__show").click(function () {
    $(this).parents(".header-contacts").toggleClass("_open")
})
$(".header-contacts__close").click(function () {
    $(this).parents(".header-contacts").removeClass("_open")
})

$("._search-field").on("input",function () {
    if($(this).val().length > 3){
        $(this).parents(".m-search").addClass("_show")
    }
})

$(document).mouseup( function(e){
    var div = $( ".m-search" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_show")){
            $(div).removeClass("_show")
        }
    }
});
